import ProjectI from '../models/project'
import projectsJson from '../projects.json'
import { Grid, Typography, Box, Chip, Button, List, ListItem } from '@mui/material'
import { useTheme } from '@mui/material/styles';
import { ReactNode } from 'react'
import ColorfulPages from './ColorfulPages'
import TaggedBox from './TaggedBox'
import LinkIcon from '@mui/icons-material/Link';

const parsedProjects: ProjectI[] = projectsJson

export default function ProjectsShowcase() {

    const theme = useTheme()


    const getSpecial = (title: string): ReactNode => {
        switch (title) {
            case "Colorful .xyz pages":
                return <ColorfulPages/>
            case "TaggedBox":
                return <TaggedBox/>
            default: break
        }
        return <></>
    } 

    const getTags = (languages: string[], frameworks: string[], tools: string[]): ReactNode => {
        return (<div style={{display:'flex', flexDirection:'column'}}>
        <Typography fontSize={'0.9em'} color={'grey'}>Tech stack</Typography>
        <div style={{display:'flex', gap:"8px", flexWrap:'wrap'}}>
            {languages.map((item, i) => {
                return <Chip key={item+i} label={item}/>
            })}    
            {frameworks.map((item,i) => {
                return <Chip key={item+i} label={item}/>
            })}    
            {tools.map((item, i) => {
                return <Chip key={item+i} label={item}/>
            })}    
        </div>
            </div>
        )

    }


    return (
        <Grid container spacing={2} paddingBottom={8}>
            { parsedProjects.map((project,i) => {
                return (
                    <Grid item xs={12} md={6} key={project.title+i}>
                        <Box sx={{background: theme.palette.background.paper , p:4, borderRadius:1, display:'flex', flexDirection:'column',gap:"16px" }}>
                            <div style={{display:'flex', flexDirection:'column'}}>
                            <div style={{display:'flex', flexDirection:'row', gap:'16px', alignItems:'start'}}>
                                <Typography fontSize={"1.2em"}>{project.title}</Typography>
                                <Chip label={project.type} color={project.type === 'work' ? 'secondary' : project.type === 'personal' ? 'primary' : 'warning'} variant='outlined'/>
                            </div>
                            <Typography fontSize={'0.9em'} color={'grey'}>{project.year}</Typography>
                            </div>
                            <Typography>{project.description}</Typography>
                            {project.accomplishments &&
                            <div>
                            
                            <Typography fontSize={'0.9em'} color={'grey'}>Accomplishments</Typography>
                            <List dense disablePadding sx={{border:`2px solid ${theme.palette.background.default}`, borderRadius:'4px'}}>
                                {project.accomplishments?.map((accomplishment, i) => {
                                    return <ListItem key={`${accomplishment}-${i}`} sx={{background: i%2 === 0 ? theme.palette.background.default : 'transparent', fontSize:'13px'}}>
                                        {accomplishment}
                                        </ListItem>
                                })}
                            </List>
                                </div>
                                }
                            {getSpecial(project.title)}
                            { project.url && <Button color='inherit' variant='outlined' href={project.url} aria-label='A link related to the project'><LinkIcon/></Button>}
                            {getTags(project.languages,project.frameworks,project.tools)}
                        </Box>
                    </Grid>
                )
            })
            }
      </Grid>
    )
}