import { ReactNode, useState, KeyboardEvent, useEffect } from 'react'
const gridSize = 7

function getRandomInt(max: number) {
  return Math.floor(Math.random() * max);
}

function getRandomPosition() {
  return [getRandomInt(gridSize), getRandomInt(gridSize)]
}

export default function GamePage() {
  const [position, setCurrentPosition] = useState([3, 3])
  const grid = [...Array(gridSize ** 2)]
  const [focused, setFocused] = useState(false)
  const [points, setPoints] = useState(0)
  const [pointPosition, setPointPosition] = useState(getRandomPosition())

  function makeStyleBlock(x: number, y: number): ReactNode {
    if (position[0] === x && position[1] === y) {
      // return <h3>{`[${x},${y}]`}</h3>
      return <p key={`${x},${y}`}>{'x'}</p>
    } else if (pointPosition[0] === x && pointPosition[1] ===y) {
      return <p key={`${x},${y}`}>{"o"}</p>
    }

    // return <p style={{ margin: '0px' }}>{`[${x},${y}]`}</p>
    return <p style={{ margin: '0px' }} key={`${x},${y}`}>{'/'}</p>
  }

  useEffect(() => {
    if (position[0] === pointPosition[0] && position[1] === pointPosition[1]) {
      setPoints((points) => points+1)
      setPointPosition(getRandomPosition())
    }
  },[position, pointPosition])

  function handleKeyDown(e: KeyboardEvent<HTMLDivElement>) {
    switch (e.key) {
      case 'ArrowUp':
        e.preventDefault()
        setCurrentPosition((position) =>
          position[0] !== 0 ? [position[0] - 1, position[1]] : position
        )
        break
      case 'ArrowLeft':
        e.preventDefault()
        setCurrentPosition((position) =>
          position[1] !== 0 ? [position[0], position[1] - 1] : position
        )
        break
      case 'ArrowRight':
        e.preventDefault()
        setCurrentPosition((position) =>
          position[1] !== gridSize - 1
            ? [position[0], position[1] + 1]
            : position
        )
        break
      case 'ArrowDown':
        e.preventDefault()
        setCurrentPosition((position) =>
          position[0] !== gridSize - 1
            ? [position[0] + 1, position[1]]
            : position
        )
        break
    }
  }

  return (
    <div
      onKeyDown={handleKeyDown}
      tabIndex={0}
      onBlur={() => {
        setFocused(false)
      }}
      onFocus={() => {
        setFocused(true)
      }}
      style={{
        position: 'relative',
        borderRadius:'8px'
      }}
    >
      {!focused && (
        <div
          style={{
            position: 'absolute',
            height: '100%',
            width: '100%',
            textAlign: 'center',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            borderRadius:'4px',
            fontFamily:'monospace'
          }}
        >
          Click and move by arrow keys
        </div>
      )}
      <p style={{textAlign:'center', fontFamily:'monospace'}}>Score: {points}</p>
      <div
        style={{
          display: 'grid',
          gridTemplate: `repeat(${gridSize},1fr) / repeat(${gridSize},1fr)`,
          justifyItems: 'center',
          alignItems: 'center',
          lineHeight: '1em',
          filter: `blur(${focused ? 0 : "4px"})`,
        }}
      >
        {grid.map((_, i) => {
          return makeStyleBlock(Math.floor(i / gridSize), i % gridSize)
        })}
      </div>
    </div>
  )
}
